.footer{
    display: flex;
    width: 100%;
    background-color: #363434;    
    align-items: center;
}

ul{
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    color: #049fe7;
}

li{
    list-style: none;
    cursor: pointer;
    font-size: 1.5em;
    text-align: center;
    width: 25%;
    transition: color 0.5s linear;
}

li:hover{
    color: #f38507;    
}

li:nth-child(3):hover{  
    animation: heartbeat 0.5s linear infinite;
}

@keyframes heartbeat{
    0% {color: #049fe7; font-size: 1.5em;}
    50% {color: #eb08b9; font-size: 2em;}
    100% {color: #049fe7; font-size: 1.5em;}
}